.App {
  text-align: center;
}
.btnSlider{
  width: 50px;
  height: 50px;
  font-size: 30px;
  background-color: #282c34;
  border: none;
  color: silver;
  transform: translateY(-350px);
}
.previousBtn{
  margin-right: 1350px;
}
.btnSlider:hover{
  background: #1a1d22;
}
.linkStyle{
  text-decoration: none;
  color: silver;
  padding:25px;
  font-size:18px;
}
.linkStyleBtn{
  color: silver;
  padding:25px;
  font-size:18px;
}
.linkStyleBtn:hover{
  color: crimson;
}
.linkStyle:hover{
  color: crimson;
}
.list{
  display: grid;
  grid-template-columns: 80%;
  justify-content: center;
  align-items: center;
  justify-items: center;
  margin-top: 15px;
}
.product-photo img{
  width: 200px;
  height: 200px;
}
.product-photo{
  border: 2px solid rgb(81, 119, 150);
}
.product-description{
  width: 500px;
}
.product-description h1{
  color: rgb(102, 99, 99);
}
.product-description h1:hover{
  color: crimson;
}
.product-description span{
  padding-left: 50px;
  color: crimson;
}
.product-item{
  border: 5px solid #282c34;
  width: 70%;
  display: flex;
  justify-content: space-around;
  padding: 20px;
}
.favori-btn:hover,.panier-btn:hover,.trash-btn:hover{
  color: rgb(97, 93, 93);
  cursor: pointer;
}
.footer-style{
  background-color: #282c34;
  margin-top: 80px;
  padding: 20px;
}
.total{
  border: 1px solid #282c34;
  font-size: 30px;
  padding: 10px;
  width: 70%;
  margin-left: 15%;
  margin-top: 40px;
}
.total span{
  margin-left: 20px;
}